<template>
  <div class="page-box">
    <HeadTop title="排行榜"></HeadTop>
    <div class="leaderboard">
      <div class="top">
        <div class="card-wrap">
          <div class="card">
            <div class="title">推广奖励池</div>
            <div class="num">{{periodInfo.rewardPool}}<span class="symbol">&nbsp;&nbsp;ABDX</span></div>
          </div>
        </div>
        <div class="tip flex-middle flex-between">
          <div class="left flex-item">我的今日分享推广数量：<span class="num" style="color:#FFE402">{{userShare}}ABDX</span></div>
          <div class="right"><div style="font-size: 13px">
            今日排行: <span>{{$dateformat(startTime*1000, 'yyyy-mm-dd HH:MM')}}</span>
            </div><div class="flex-middle">规则<div class="img-wrap" @click="isShowDialog = true"><img src="@/assets/images/help@2x.png" alt=""></div></div>
          </div>
        </div>
      </div>
      <div class="tab-wrap">
        <van-tabs v-model="cid" type="card" @click-tab="tabHandle">
          <van-tab name="0" title="今日排行榜"></van-tab>
          <van-tab name="1" title="昨日排行榜"></van-tab>
        </van-tabs>
      </div>
      <div class="tab-content-wrap">
        <div class="first-wrap" v-if="periodInfo.topEleven.length">
          <div class="item">
            <div class="top">{{periodInfo.topEleven[0].address.substring(0,5)+'....'+periodInfo.topEleven[0].address.substring(periodInfo.topEleven[0].address.length-5,periodInfo.topEleven[0].address.length)}}</div>
            <div class="bottom">钱包地址</div>
          </div>
          <div class="item">
            <div class="top">{{periodInfo.topEleven[0].amount}}</div>
            <div class="bottom">质押数量(ABDX)</div>
          </div>
          <div class="item">
            <div class="top">{{periodInfo.topEleven[0].reward}}</div>
            <div class="bottom">ABDX奖励(预估)</div>
          </div>
          <div class="img-wrap"><img src="@/assets/images/no1@2x.png" alt=""></div>
        </div>
        <div class="img-wrap top10"><img src="@/assets/images/top10@2x.png" alt=""></div>
        <div class="table-wrap">
          <div class="thead">
            <div class="th">序号</div>
            <div class="th">钱包地址</div>
            <div class="th">质押数量(ABDX)</div>
            <div class="th">奖励(预估)</div>
          </div>
          <div class="tbody">
            <div class="tr border-1px-b" v-for="(item,index) in periodInfo.topTen">
              <div class="td">
                <template v-if="index<3">
                  <img class="img" :src="require('@/assets/images/top'+index+'@2x.png')" alt="">
                </template>
                <template v-else>
                  {{index + 1}}
                </template>
              </div>
              <div class="td">{{item.address.substring(0,4)+'....'+item.address.substring(item.address.length-4, item.address.length)}}</div>
              <div class="td">{{item.amount}}</div>
              <div class="td">{{item.reward}} ABDX</div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="rule-wrap">
        <van-dialog v-model:show="isShowDialog" title="规则" :show-cancel-button="false" :showConfirmButton="false" :closeOnClickOverlay="true">
            1. 第一名可拿奖金池 10%<br>
            2. 第2-11名可拿奖金池10%，平均分配
          </van-dialog> 
    </div>
     
  </div>
</template>
<script>
import HeadTop from '@/components/HeadTop' 
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import {useStore} from "vuex";
import config from "@/utils/config";
import tokenAbi from "@/utils/abi";
import investAbi from "@/utils/investAbi";
import { Toast } from 'vant';
import BigNumber from 'bignumber.js';
export default {
  components: {
    HeadTop
  },
  setup() {
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      investContract: null,
      abdxContract: null,
      startTime: 0,
      isShowDialog: false,
      userShare: 0,
      initContract: () => {
        data.investContract = new data.web3.eth.Contract(investAbi, config.investAddress)
        data.abdxContract = new data.web3.eth.Contract(tokenAbi, config.abdxAddress)
        data.initData()
      },
      cid: '0',
      tabHandle: (inject) => {
        if(inject.name == '0') {
          data.getCurRank()
        } else {
          data.getYesRank()
        }
      },
      periodInfo: {
        rewardPool: 0,
        topEleven: [],
        topTen: []
      },
      initData: () => {
        data.getCurRank()
        data.getUserShare()
        data.getCurStartTime()
        data.getAllUserRank()
        // data.getAllRank()
      },
      getCurStartTime: async () => {
        let periodNum = await data.investContract.methods.periodNum().call()
        let periodInfo = await data.investContract.methods.getPeriodInfo(periodNum).call()
        data.startTime = periodInfo.startTime
      },
      getCurRank: async () => {
         data.periodInfo.topEleven = []
         data.periodInfo.topTen = []
         let decimals = await data.abdxContract.methods.decimals().call()
         let periodNum = await data.investContract.methods.periodNum().call()
         let periodInfo = await data.investContract.methods.getPeriodInfo(periodNum).call()
         let topEleven = await data.investContract.methods.getTopEleven().call()
         let totalReward = new BigNumber(periodInfo.rewardPool).dividedBy(10**18).toFixed(2)
         data.periodInfo.rewardPool = totalReward;
         for (let i = 0; i <topEleven.length; i++) {
           let userAmount = await data.investContract.methods.getPeriodShareAmount(periodNum, topEleven[i]).call()
           let params = {
             address: topEleven[i],
             amount: new BigNumber(userAmount).dividedBy(10**decimals).toFixed(2),
             reward: i == 0 ? new BigNumber(totalReward).dividedBy(10).toFixed(2) : new BigNumber(totalReward).dividedBy(100).toFixed(2)
           }
           data.periodInfo.topEleven.push(params);
           if(i>=1) {
             data.periodInfo.topTen.push(params);
           }
         }
         console.log(data.periodInfo.topTen)
      },
      getAllRank: async () => {
         let periodNum = await data.investContract.methods.periodNum().call()
         for (let i = periodNum; i >= 1; i--) {
           let periodInfo = await data.investContract.methods.getPeriodInfo(i).call()
           console.log(periodInfo, i + '------------')
         }
      },
      getAllUserRank: async () => {
         let periodNum = await data.investContract.methods.periodNum().call()
         for (let i = periodNum; i >= 1; i--) {
           let periodInfo = await data.investContract.methods.getUserPeriods(data.address,i).call()
           let params = {
             amount: Number(periodInfo.amount/10**18).toFixed(4),
             isPush: periodInfo.isPush
           }
           console.log(params, i + '------------')
         }
      },
      getUserShare: async () => {
         let decimals = await data.abdxContract.methods.decimals().call()
         let periodNum = await data.investContract.methods.periodNum().call()
         let userAmount = await data.investContract.methods.getPeriodShareAmount(periodNum, data.address).call()
         data.userShare = new BigNumber(userAmount).dividedBy(10**decimals).toFixed(4)
      },
      getYesRank: async () => {
         data.periodInfo.topEleven = []
         data.periodInfo.topTen = []
         let decimals = await data.abdxContract.methods.decimals().call()
         let periodNum = await data.investContract.methods.periodNum().call()
         periodNum = periodNum -1;
         let periodInfo = await data.investContract.methods.getPeriodInfo(periodNum).call()
         let topEleven = periodInfo.topEleven
         let totalReward = new BigNumber(periodInfo.rewardPool).dividedBy(10**18).toFixed(2)
         data.periodInfo.rewardPool = totalReward;
         for (let i = 0; i <topEleven.length; i++) {
           let userAmount = await data.investContract.methods.getPeriodShareAmount(periodNum, topEleven[i]).call()
           let params = {
             address: topEleven[i],
             amount: new BigNumber(userAmount).dividedBy(10**decimals).toFixed(2),
             reward: i == 0 ? new BigNumber(totalReward).dividedBy(10).toFixed(2) : new BigNumber(totalReward).dividedBy(100).toFixed(2)
           }
           data.periodInfo.topEleven.push(params);
           if(i>=1) {
             data.periodInfo.topTen.push(params);
           }
         }
      },
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>

<style lang="less" scoped>
.leaderboard {
  & > .top {
    background: #0770fe url(~@/assets/images/leaderboard_bg@2x.jpg) no-repeat;
    background-size: 100% 100%;
    color: #fff;
    padding-bottom: 70px;
    .card-wrap {
      background: url(~@/assets/images/leaderboard_card@2x.png) no-repeat;
      background-size: 100% 100%;
      width: 335px;
      min-height: 160px;
      margin: 0 auto;
      position: relative;
      display: flex;
      justify-content: center;
      font-weight: 700;
      .title {
        color: #ffe402;
        font-size: 15px;
        text-align: center;
        padding-top: 50.5px;
        padding-bottom: 19px;
      }
      .num {
        font-size: 24px;
        word-break: break-all;
        line-height: 1;
        max-width: 295px;
        padding-bottom: 40px;
      }
      .symbol {
        font-size: 20px;
      }
    }
    .tip {
      font-size: 15px;
      .left {
        padding-left: 15px;
      }
      .num {
        font-weight: 700;
      }
      .right {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        // align-items: center;
         padding-right: 15px;
        text-align:right;
        .img-wrap {
          margin: 0 0 0 10px;
          img {
            width: 17px;
            height: 17px;
            display: block;
          }
        }
      }
    }
  }
  .tab-wrap {
    position: relative;
    margin-top: -45px;
  }
  .tab-content-wrap {
    background: #f6f6f6;
    padding: 12px 12px;
    .first-wrap {
      margin-top: 23.5px;
      background: url(~@/assets/images/leaderboard_card_bg@2x.png) no-repeat;
      background-size: 100% 100%;
      display: flex;
      padding: 0 23.5px;
      position: relative;
      .img-wrap {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 100px;
        height: auto;
        img {
          width: 100%;
          height: 100%;
        }
      }
      .item {
        text-align: center;
        width: 33.33%;
        word-break: break-all;
        padding-top: 41px;
        padding-bottom: 14px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        .top {
          padding-bottom: 3.5px;
          color: #f01c00;
          font-size: 14px;
          font-weight: 700;
        }
        .bottom {
          font-size: 12px;
          color: #8e856b;
        }
      }
    }
    .top10 {
      width: 100%;
      height: auto;
      margin: 17px 0 12px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .table-wrap {
      border-radius: 6px;
      overflow: hidden;
      .thead {
        display: flex;
        align-items: center;
        background: #e1e8fc;
        min-height: 40px;
        .th {
          color: #606467;
          width: 29%;
          text-align: center;
          word-break: break-all;
          &:nth-child(1) {
            width: 13%;
            text-align: center;
          }
        }
      }
      .tbody {
        .tr {
          display: flex;
          align-items: center;
          background: #fff;
          min-height: 55px;
          &:last-child {
            &::after {
              display: none;
            }
          }
          .img {
            width: 30px;
            height: auto;
            display: block;
          }
          .td {
            padding: 3px;
            width: 29%;
            text-align: center;
            word-break: break-all;
            font-size: 14px;
            color: #111;
            &:nth-child(1) {
              width: 13%;
              display: flex;
              justify-content: center;
              align-items: center;
              font-size: 16px;
              font-weight: 700;
              color: #1f2428;
            }
          }
        }
      }
    }
  }
}
</style>
